.boxContainer {
    position: relative;
    margin: 25px auto;
    width: 90vw;
    max-width: 600px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 1px;

    .box {
        border: 1px solid #ccc;
        height: calc( (90vw - 5px) / 4);
        max-height: 150px;

        .boxInner {
            width: 100%;
            height: 100%;
            position: relative;
            transition: transform 1s;
            transform-style: preserve-3d;
        }
    }
}

.cardFace {
    position: absolute;
    height: 100%;
    width: 100%;
    backface-visibility: hidden;
}

.cardFaceFront {
    background: lightgreen;
    display: flex;
    justify-content: center;
    align-items: center;
    // font-size: 20px;
}

.cardFaceBack {
    background: skyblue;
    transform: rotateY( 180deg );
    .gameImg {
        width: 100%;
        height: 100%;
    }
}

.isFlipped {
    transform: rotateY(180deg);
}
